<template>
  <div>
    <span v-if="linkContent">{{ `sent ${linkContent} to ${platform}` }}</span>
  </div>
</template>

<script>
export default {
  name: "Access",
  data() {
    return {
      platform: "",
      linkContent: "",
    };
  },
  created() {
    this.platform = this.$route.meta.platform || "";
  },
  mounted() {
    this.linkContent = this.$getCookie("link");
    if (this.platform === "IOS") {
      if (window.webkit) {
        window.webkit.messageHandler.getAppLink.postMessage(this.linkContent);
      }
    }
    if (this.platform === "Android") {
      if (window.Android) {
        window.Android.getAppLink(this.linkContent);
      }
    }
  },
};
</script>
